import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

const initialState = {};

@observer
class Tracks extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadTracks();
    }

	loadTracks() {
		const { trackStore } = this.props.stores;
		trackStore.load();
	}

	render() {
		const { trackStore, userStore } = this.props.stores;
		const { tracks } = trackStore;
		const { user } = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12'>
						<h5>Treningsrundene dine - {tracks.length} stk</h5>
						<div class='row'>
							{tracks && tracks.map(track => (
								<div class='col-12 col-sm-6 col-lg-2 mt-4 p-2 d-flex'>
									{/* {JSON.stringify(track, null, 4)} */}

									<div class={`text-center border rounded-circle imageRounded`} style={track.image ? `background-image: url("${track.image.s3SmallLink}");` : ''}>
										{!track.image && <i class='fas fa-map-marked text-muted mt-3' style='font-size: 40px;' />}
									</div>
									<div class='flex-grow-1 pl-3'>
										<a class='stretched-link' href={`/tracks/${track.id}`}>
											<h5>{track.name}</h5>
										</a>
										<small class='text-muted'>
											<i class='fas fa-road' /> {track.distanceKm} km
											<i class='fas fa-mountain ml-2' /> {track.elevation} m
										</small>
									</div>
								</div>
							))}
						</div>

						<div class='col-12'>
							<a class='btn btn-primary mt-5 float-right'
								href='/tracks/new'
							>
								<i class='fas fa-plus' /> Legg til runde
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Tracks;
